.offcanvas-panel {
    position: fixed;
    right: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    transition: all 0.3s ease-out 0s;
    opacity: 0;
    visibility: hidden;
}
.offcanvas-panel.panel-on {
    visibility: visible;
    opacity: 1;
}
.offcanvas-panel.panel-on .offcanvas-panel-inner {
    visibility: visible;
    opacity: 1;
    margin-right: 0;
}
.offcanvas-panel .offcanvas-panel-inner {
    max-width: 400px;
    height: 100%;
    background-color: #fff;
    padding: 40px;
    position: relative;
    overflow-y: scroll;
    overflow-x: hidden;
    margin-right: -400px;
    transition: all 0.3s ease-out 0s;
    opacity: 0;
    visibility: hidden;
