/* Hero Area V6 (Home V6) */
.hero-slide-single {
    padding-top: 266px;
    padding-bottom: 199px;
    position: relative;
    z-index: 1;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-position: center center;
}
.hero-slide-single:after {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    background-image: -ms-linear-gradient( 135deg, rgba(9,16,35,0.2902) 0%, rgba(6,14,34,0.58) 61%, rgba(4,13,34,0.87843) 100%);
}
.hero-slider .slick-arrow {
    background-color: #3377FF;
    position: absolute;
    bottom: 0;
    right: 70px;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 50px;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    z-index: 5;
}
.hero-slider .slick-arrow.slick-disabled {
    background-color: #FFFFFF;
    color: #2b354f;
}
.hero-slider .next.slick-arrow {
    right: 15px;
    left: auto;
}
.hero-slider .slick-dots {
    position: absolute;
    top: 0;
    left: auto;
    right: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: auto;
}
.hero-slider .slick-dots li {
    display: block;
    height: 12px;
    width: 12px;
    background: #FFFFFF;
    border-radius: 50px;
    margin: 15px  0;
    position: relative;
    margin-left: auto;
    margin-right: 9px;
}
.hero-slider .slick-dots li.slick-active {
    background-color: #3377ff;
}
.hero-slider .slick-dots li:after {
    position: absolute;
    content: "";
    height: 30px;
    width: 30px;
    background: transparent;
    z-index: -1;
    border-radius: 50px;
    right: -9px;
    top: -9px;
    border: 2px solid #3377ff;
    transition: 0.3s;
    transform: scale(0);
    z-index: 1;
}
.hero-slider .slick-dots li.slick-active:after {
    transform: scale(1);
}
.hero-area.hero-v6 .section-title h1 {
    margin-bottom: 30px;
    font-size: 80px;
