/* 11. Team Member Area */

/* Single Team Member */
.single-team-member {
    position: relative;
    margin-bottom: 30px;
}
.single-team-member.fadeInUp{
    z-index: 5;
}
.single-team-member .team-member-thumb {
    overflow: hidden;
    position: relative;
    z-index: 3;
}
.single-team-member .team-member-thumb:before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f4732a;
    opacity: 0;
    border-radius: 3px;
    transition: 0.4s;
    transform: perspective(400px) rotateX(-90deg);
    transform-origin: top;
    border-radius: 6px;
    z-index: 2;
}
.single-team-member:hover .team-member-thumb:before {
    opacity: 0.6;
    transform: perspective(400px) rotateX(0deg);
}
.single-team-member .team-member-thumb img {
    border-radius: 7px;
    width: 100%;
}
.single-team-member .team-member-bio {
    background-color: #fff;
    border-radius: 7px;
    padding: 30px 20px;
    box-shadow: 0px 10px 60px 0px rgba(215, 212, 255, 0.6);
    text-align: center;
    position: absolute;
    transition: 0.4s;
    opacity: 0;
    visibility: hidden;
    bottom: -180px;
    width: 90%;
    left: 13px;
    z-index: 4;
}
.single-team-member .team-member-bio .team-member-name {
    font-size: 20px;
    color: #1a1b1e;
    line-height: 30px;
}
.single-team-member .team-member-bio .team-member-role {
    font-size: 15px;
    font-family: "Poppins", sans-serif;
    color: #f4732a;
    line-height: 25px;
}
.single-team-member .social-links {
    margin-top: 13px;
}
.social-links ul li {
    margin-right: 8px;
    transition: all .3s linear;
    display: inline-block;
    margin-right: 8px;
}
.social-links ul li:last-of-type {
    margin: 0;
}
.social-links ul li a i {
    background-color: #E5EEFF;
    height: 35px;
    width: 35px;
    text-align: center;
    line-height: 35px;
    color: #0057ff;
    border-radius: 50px;
    font-size: 13px;
}
.social-links ul li a i.fa-facebook-f {
    background-color: #0040E5;
    color: #fff;
}
.social-links ul li a i.fa-twitter {
    background-color: #DDEFFB;
    color: #55aded;
}
.social-links ul li a i.fa-youtube {
    background-color: #FFE5E5;
    color: #ff0303;
}
.social-links ul li a i.fa-behance {
    background-color: #E5EEFF;
    color: #0057ff;
}
.single-team-member .social-links ul li{
    visibility: hidden;
    opacity: 0;
    transform: rotateY(-70deg);
    transform-origin: left;
    position: relative;
    left: 3px;
}
.single-team-member:hover .social-links li {
    visibility: visible;
    opacity: 1;
    transform: rotateY(0deg);
}
.single-team-member:hover .social-links li:nth-child(2) {
    transition-delay: .2s;
}
.single-team-member:hover .social-links li:nth-child(3) {
    transition-delay: .3s;
}
.single-team-member:hover .social-links li:nth-child(4) {
    transition-delay: .4s;
}
.single-team-member:hover .team-member-bio {
    opacity: 1;
    visibility: visible;
    bottom: -100px;
}

